import { useState } from "react";
import styled from "styled-components";
import ReactMarkdown from "react-markdown";

function App() {
  const [markdown, setMarkdown] = useState(
    "# Welcome to my React Markdown Previewer!\n## This is a sub-heading...\n### And here's an image:\n![freeCodeCamp Logo](https://cdn.freecodecamp.org/testable-projects-fcc/images/fcc_secondary.svg)"
  );

  return (
    <Wrapper>
      <section className='text'>
        <div className='heading'>
          <h3>Editor</h3>
        </div>
        <textarea
          className='input'
          cols='30'
          rows='10'
          value={markdown}
          onChange={(e) => setMarkdown(e.target.value)}
        ></textarea>
      </section>
      <article className='result'>
        <div className='heading'>
          <h3>Previewer</h3>
        </div>
        <div className='text-result'>
          <ReactMarkdown>{markdown}</ReactMarkdown>
        </div>
      </article>
    </Wrapper>
  );
}

const Wrapper = styled.main`
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 30px 20px;
  .text {
    width: 550px;
    display: grid;
    place-items: center;
    @media (max-width: 580px) {
      width: 100%;
    }
    .input {
      width: 100%;
      height: 100%;
      padding: 10px;
      font-size: 1rem;
      overflow: scroll;
      border: 1px solid black;
      border-top: 0;
      outline: none;
    }
  }
  .result {
    width: 650px;
    background-color: var(--light-pink);
    @media (max-width: 670px) {
      width: 100%;
    }
    .text-result {
      width: 100%;
      word-wrap: break-word;
      padding: 10px;
      border: 1px solid black;
      border-top: 0;
    }
  }
`;

export default App;
